@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;600;700;900&family=Open+Sans:wght@400;600;700&display=swap');

:root {
  --text-color: #323232;
  --h-color: #CEE4EB;
  --text-gray: #989898;

  --nav-background: #FFFFFF;
  --background: #E5E5E5;
  --btn-purple: #5761B2;


  --font-head: "Fira Sans", sans-serif;
  --font-body: "Open Sans", sans-serif;
  --font-size: 16px;

  --nav-full-height: 120px;
  --nav-mobile-height: 55px;

  --navigation-menu-height: 40px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important; 
  -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important; 
  outline: none !important;
} 
html {
  box-sizing: border-box;
}

body {
  color: var(--text-color);
  margin: 0;
  font-family: var(--font-body);
  font-size: var(--font-size);
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

section,
footer {
  position: relative;
}

a {
  text-decoration: none;
}

svg {
  margin: 0;
}

/* Typography */

h1,
h2,
h3,
h4 {
  font-family: var(--font-head);
  line-height: 1.2;
  padding: 0;
  margin: 0;
}

h1 {
  font-size: 3.75rem;
}

h2 {
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 1px;
}

h3 {
  font-size: 1.5rem;
  font-weight: 600;
  font-family: var(--font-body);
}

h4 {
  font-size: 18px;
}

p {
  font-style: normal;
  font-weight: normal;
  line-height: 1.375;
  margin: 0;

  @media(max-width: 510px) {
    font-size: 14px;
  }
}


/* Main */

.main {
  display: flex;
  min-height: calc(100vh - var(--nav-full-height));
  background: linear-gradient(180deg, #6CAEC1 0%, #3B93AD 100%);
  align-items: center;
  transition: ease 0.5s;

  @media (max-width: 768px) {
    min-height: calc(100vh - var(--nav-mobile-height));
  }

  h2,
  h3 {
    color: var(--h-color);
  }

  h2 {
    z-index: 1;
    text-align: right;
    letter-spacing: 0;
    padding-right: 65px;

    &::before {
      position: relative;
      right: -100%;
      content: "";
      display: block;
      width: 50px;
      height: 50px;
      border-top: 15px solid var(--h-color);
      border-right: 15px solid var(--h-color);
    }
  }

  em {
    font-style: normal;
    color: rgba(255, 255, 255, 0.9);
  }

  .author {
    margin-bottom: 2em;
  }

  .column {
    padding: 0 24px;
  }
}

.quote {
  position: absolute;
  top: 10%;
  left: 0;
  z-index: 0;

  @media (max-width: 1366px) {
    transform: scale(.6)
  }

  @media(max-width: 1024px) {
    top: 0;
    transform: scale(.5);
  }
}

.testimonial {
  margin-top: 1.5em;
  margin-bottom: 2em;
  text-transform: uppercase;

  @media (max-width: 640px) {
    font-size: 1.75rem !important;
    line-height: 1.25;
    margin-bottom: 1.5em;
  }

  @media (max-width: 500px) {
    font-size: 1.4rem !important;
    line-height: 1.25;
    margin-bottom: 1.5em;
  }

  @media (max-width: 1280px) {
    padding: 0 20px;
  }
}

/* Business domain */

.business-domain {
  text-align: center;
}

.capabilities {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 360px;
  min-width: 300px;
  margin:0 auto;
  margin-bottom: 60px;

  p {
    margin: 0;
  }

  h4 {
    margin: 30px 0;
  }

  @media (min-width: 640px) and (max-width: 1180px) {
    width: 100%;
    max-width: none;
  }

  @media (max-width: 1024px) {
    min-width: 0;
    margin-bottom: 50px;

    h4 {
      margin: 20px 0;
    }
  }
}

.icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75px;
  height: 75px;
  border: 1.5px solid #5761B2;
  border-radius: 5px;

  @media(max-width: 510px) {
    width: 50px;
    height: 50px;
  }
}

/* Tech stack */

.tech-stack-container {
  display: flex;
  align-items: stretch;

  @media (max-width: 860px) {
    flex-direction: column;

    .stack {
      flex-direction: row;
      flex: 1;
      justify-content: space-between;
    }

    .capabilities {
      flex-direction: column;
      align-items: center;
    }
  }
}

.tech-stack {
  h4 {
    margin: 0 0 50px 0;

    @media(max-width: 768px) {
      margin: 0 0 20px 0;
    }
  }

  @media (min-width: 640px) and (max-width: 1180px) {
    margin: 0;
  }
}

.stack {
  display: flex;
  flex: 1 1 200px;
  flex-direction: column;
  margin: 0 7px;
  @media (max-width: 860px) {
    margin: 0;
  }
}

@media (max-width: 860px) {
  .tech-card:first-child {
    margin-left: 0;
  }

  .tech-card:last-child {
    margin-right: 0;
  }
}

@media (min-width: 860px) {
  .stack:first-child {
    margin-left: 0;
  }

  .stack:last-child {
    margin-right: 0;
  }
}

.tech-card {
  display: flex;
  flex: 1;
  justify-content: center;
  background: #FFFFFF;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  min-height: 70px;
  margin: 7px 0;
  img {
    object-fit: none;
    height: 100%;
    width: 100%;
  }

  @media (max-width: 1180px) {
    min-height: 60px;
    img {
      transform: scale(.9);
    }
  }

  @media (max-width: 1000px) {
    img {
      object-fit: contain;
      transform: scale(.7);
    }
  }

  @media (max-width: 860px) {
    height: 60px;
    margin: 7px 7px;
    img {
      object-fit: contain;
      transform: scale(.65);
    }
    #kubernetes {
      transform: scale(.7);
    }
    #react {
      transform: scale(.8);
    }

  }

  @media (max-width: 620px) {
    min-height: 40px;
    #kubernetes {
      transform: scale(.8);
    }
  }

  @media (max-width: 510px) {
    height: 35px;
    margin: 5px 3px;
    img {
      transform: scale(.6);
    }
  }
}

.bg-possibilities {
  @include bg-text('możliwości', -125px);

  @media(max-width: 1024px) {
    @include bg-text('możliwości', -66px);
  }
}

html[lang="en"] .bg-possibilities:after {
  content: "possibilities";
}

/* Contact us / Quote */
.contact-us {
  display: flex;
  position: relative;
  height: 365px;

  @media (max-width: 768px) {
    height: 200px;
  }
}

.bg-inspire {
  display: absolute;
  background: linear-gradient(135deg, rgba(87, 97, 178, .5) 0%, rgba(31, 197, 168, .5) 100%);
  height: 365px;
  z-index: -10;

  @media (max-width: 768px) {
    height: 200px;
  }
}

.inspire-header {
  color: white;
  margin-bottom: 50px;
  font-family: var(--font-head);
  letter-spacing: 10px;

  span {
    display: block;
    font-size: 40px;
    font-weight: 900;
    line-height: 52px;
  }

  .large-text {
    font-size: 60px;
    line-height: 72px;
  }

  @media (max-width: 768px) {
    margin-bottom: 27px;

    span {
      font-size: 18px;
      line-height: 25px;
      letter-spacing: 5px;
      font-weight: 700;
    }

    .large-text {
      font-size: 27px;
      line-height: 36px;
      font-weight: 900;
    }
  }
}

/* General */

.schedule-meeting-button {
  display: flex;
  height: 40px;
  font-size: 14px;

  a {
    padding: 7.5px 25px;
    border: 1px solid black;
  }
}

/* Portfolio */
.portfolio {
}

.project-header {
  display: none;

  @media (max-width: 1128px) {
    display: inline;
  }
}

.project-content {
  display: flex;
  flex-direction: column;

  a {
    &:last-child {
      margin-top: auto;
    }
  }
}

.desc-placeholder {
  position: absolute;
  visibility: hidden;
  z-index: -100;
  height: 1000px;
}

.slide-desc {
  min-height: 200px;
  transform: translate(0, 30px);
  transition: all 1s ease;
  p:last-of-type {
    margin-bottom: 0;
  }
}

.active {
  visibility: visible;
  transform: translate(0);
  opacity: 1;
}

.slider {
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.15);
}

.small-slider {
  display: none;
}

.project-title {
  margin: 0 0 20px 0;
  letter-spacing: 1px;
  text-align: left;

  @media (max-width: 1023px) {
    text-align: center;
  }
}

.project-description {
  margin-bottom: 15px; 
  line-height: 1.2;

  a {
    font-weight: bold;
    color: initial;
    text-decoration: underline;
  }
}

/* Clients */

.clients {
  margin: 0;
  text-align: center;
}


.client-flex-items {
  display: flex;
  flex-direction: column;
  @media (max-width: 1055px) {
    flex-direction: row;
    justify-content: center;
  }
}

.client-flex-items > .client-logo-list:first-of-type {
  margin-bottom: 40px;
  @media (max-width: 1055px) {
    margin-bottom: 0;
  }
}

.client-logo-list {
  display: flex;
  justify-content: space-between;
  @media (max-width: 1055px) {
    flex-direction: column;
    &:first-of-type {
      margin-right: 80px;
    }
  }
  @media (max-width: 790px) {
    &:first-of-type {
      margin-right: 50px;
    }
  }
  @media (max-width: 640px) {
    width: calc((100vw - 40px) / 2);
    &:first-of-type {
      margin-right: 0;
    }
  }
}

.client-logo {
  height:70px;
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    height: 70px;
  }
  @media (max-width: 1280px) {
    transform: scale(.9);
  }
  @media (max-width: 1090px) {
    transform: scale(.8);
  }
  @media (max-width: 1055px) {
    transform: scale(1);
    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }
  @media (max-width: 790px) {
    transform: scale(.9);
  }
  @media (max-width: 640px) {
    transform: scale(.75);
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  @media (max-width: 510px) {
    &:not(:last-child) {
      margin-bottom: 0;
    }
  }
}

#healthly {
  svg {
    height: 60px;
  }
}

#info-group {
  svg {
    height: 50px;
  }
}

/* Background svg */

.bg-pattern {
  img {
    width: 493px;
    height: 100%;
    position: absolute;
    right: 0%;
    z-index: -1;
    mask-image: linear-gradient(67deg, rgba(2, 0, 36, 0) 0%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 1) 100%);
    -webkit-mask-image: linear-gradient(67deg, rgba(2, 0, 36, 0) 0%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 1) 100%);
    @media (max-width: 768px) {
      max-width: 270px;
      height: 100%;
      mask-image: linear-gradient(67deg, rgba(2, 0, 36, 0) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 1) 100%);
      -webkit-mask-image: linear-gradient(67deg, rgba(2, 0, 36, 0) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 1) 100%);
    }
  }
}

.bg-projects {
  @include bg-text('projekty', -126px);

  @media(max-width: 1024px) {
    @include bg-text('projekty', -62px);
  }
}

html[lang="en"] .bg-projects:after {
  content: "projects";
}

.bg-plus-1 {
  @include bg-item(3%, 14%);

  @media(max-width: 1024px) {
    @include bg-item(17%, 10%);
    transform: scale(.5);
  }
}

.bg-plus-2 {
  @include bg-item(16%, 78%);

  @media(max-width: 1024px) {
    @include bg-item(37%, 60%);
    transform: scale(.5);
  }
}

.bg-plus-3 {
  @include bg-item(42%, 51%);

  @media(max-width: 1024px) {
    @include bg-item(70%, 5%);
    transform: scale(.5);
  }
}

.bg-arrow {
  @include bg-item(56%, 31%, .12);

  @media(max-width: 1024px) {
    @include bg-item(55%, 10%, .12);
    transform: scale(.5);
  }
}

.bg-arrow-2 {
  @include bg-item(3%, 80%, .12);

  @media(max-width: 1024px) {
    @include bg-item(40%, 60%, .12);
    transform: scale(.5);
  }
}

.sm-slider {
  display: none;
}

/* Media queries */

@media (max-width:959px) {

  //.menu {
  //  display: none;
  // }
  // hamburger menu to do
  .main {
    h2 {
      font-size: 2rem;
    }
  }
}

@media (max-width:1024px) {
  .main {
    height: calc(100% - 60px);
  }

  h2.testimonial {
    margin-right: 0;
    padding: 0;

    &::before {
      display: none;
    }
  }

  .project-content {
    text-align: center;

    h2.underline-start {
      &::after {
        left: calc(50% - 25px);
      }
    }

    .project-description {
      text-align: left;
    }
  }

  .inspire {
    height: auto;
  }

  #testimonial-swiper .column {
    max-width: 1280px;
    padding-bottom: 0;
  }
}



@media (max-width: 510px) {
  .quote {
    top: 0;
  }

  h2 {
    font-size: 18px;
    line-height: 1.5;
  }

  .main {

    h2,
    h3 {
      text-align: center;
    }

    h3 {
      font-size: 1rem;
      font-weight: 400;
      line-height: 2rem;
    }
  }

  .icon-wrapper {
    svg {
      transform: scale(.66);
    }
  }
}

.client-header {
  display: none;

  @media (min-width: 791px) {
    display: inline;
  }
}

.client-header-sm {
  display: none;

  @media (max-width: 790px) {
    display: inline;
    margin-bottom: 40px;
  }
  @media (max-width: 510px) {
    margin-bottom: 20px;
  }
}

// COOKIES
.cookies {
  display: none;
  position: fixed;
  text-align: center;
  bottom: 0;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, .9);
  width: 100%;
  font-size: 0.7em;
  padding: 7px;
  z-index: 100;
  color: white;
}

.confirm-cookies {
  font-size: 15px;
  border: 1px solid white;
  padding: 5px 0;
  cursor: pointer;
  max-width: 250px;
  margin: 0 auto;
  text-align: center;
  color: white;
}

.cookies-text {
  font-size: 15px;
  margin-bottom: 10px;

  @media (max-width: 1200px) {
    font-size: 12px;
  }
}

.confirm-cookies:hover {
  background-color: rgba(0, 0, 0, 0.5);
}