/* Global */

.column {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1280px;  // HD ready
  margin: 0px auto;
  justify-content: center;
  padding: 100px 20px;
  @media (max-width: 768px) {
    padding: 50px 20px;
  }
}

.fullheight {
  min-height: calc(100vh - var(--nav-full-height));
}

.underline {
  &::after {
      display:block;
      position: relative;
      left: calc(50% - 25px);
      content:'';
      height: 20px;
      width: 50px;
      border-bottom: 1px solid #5761B2;
      @media(max-width: 510px) {
        width: 25px;
      left: calc(50% - 12.5px);
      }
    }
}

.underline-start {
  &::after {
      display:block;
      position: relative;
      content:'';
      height: 20px;
      width: 50px;
      border-bottom: 1px solid #5761B2;
      @media(max-width: 510px) {
        width: 25px;
      }
    }
}

.w-100 {
  width: 100%;
}

.mb-20 {
  margin-bottom: 20px;
}

.mb-30 {
  margin-bottom: 30px;
}

.mb-40 {
  margin-bottom: 40px;
  @media(max-width: 510px) {
    margin-bottom: 30px;
  }
}

.mb-50 {
  margin-bottom: 50px;
  @media(max-width: 510px) {
    margin-bottom: 40px;
  }
}

.mb-60 {
  margin-bottom: 60px;
  @media(max-width: 510px) {
    margin-bottom: 50px;
  }
}

.flex {
  display: flex;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.m-top-auto {
  margin-top: auto;
}

.m-left-auto {
  margin-left: auto;
}

.btn-large {
  display: inline-block;
  color: white;
  font-size: 14px;
  background-color: var(--btn-purple);
  width: 300px;
  height: 40px;
  line-height: 40px;
  font-weight: 600;
  text-align: center;
  @media(max-width: 500px) {
    width: 100%;
    font-size: 12px;
    font-weight: 600;
  }
  span {
    vertical-align: baseline;
  }
}

.bold {
  font-weight: 700;
}

.bg {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
  pointer-events: none;
  .column {
    height: 100%;
  }
}

@mixin bg-text($content, $positionY) {
  &::after {
    content: $content;
    font-family: var(--font-head);
    font-size: 475px;
    font-weight: bold;
    color: #F9F9F9;
    position: absolute;
    bottom: $positionY;
    left: 50%;
    transform: translateX(-50%);
    z-index: -9;
    @media(max-width: 1024px) {
      font-size: 250px;
    }
  }
}

  @mixin bg-item($top, $left, $opacity: 0.25, $scale: 1) {
  position: absolute;
  left: $left;
  top: $top;
  z-index: -3;
  opacity: $opacity;
  transform: scale($scale);
  filter: drop-shadow(10px 10px 10px rgba(0, 0, 0, .4));
}

.text-center {
  text-align: center;
}

.mb-1 {
  margin-bottom: 1em;
}
