.about-header {
  position: relative;
  display: flex;
  height: 215px;
  color: #E7F1F5;
  background: url(../images/about-header.png) ;
  text-align: center;
  margin-top: var(--nav-full-height);
  h1 {
    &::after {
      border-bottom: 1px solid #E7F1F5;
    }
  }
  @media (max-width: 767px) {
    margin-top: var(--nav-mobile-height);
  }
}

.header-bg {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #3B93AD;
  opacity: 0.85;
  .column {
    padding: 0 20px;
  }
}

.article {
  @media (max-width: 770px)
  {
      flex-direction: column;
      display: flex;
  }
  p {
    margin-bottom: 20px;
    order:2;
  }
  p + p {
    order:3;
  }
  p + p + p {
    order:5;
  }
  p + p + p + p{
    order:6;
  }
  p:last-child {
    margin-bottom: 75px;
  }
  h2 {
    order:1;
    margin-bottom: 30px;
    @media (max-width: 770px)
    {
      font-size: calc((100vw - 40px)/17.5);
    }
  }
  img {
    order:4;
    width: 500px;
    height: auto;
    float: right;
    margin: 0 0 2.5em 2.5em; 
    @media (max-width: 770px)
    {
      float: none;
      margin:auto;
      margin-bottom: 20px;
    }
    @media (max-width: 540px)
    {
      width: calc(100vw - 40px);
      height: auto;
    }
  }
}

section.description > .bg > .column > .bg-arrow {
  @include bg-item(25%, 10%, .12);
}

section.description > .bg > .column > .bg-plus-2 {
  @include bg-item(70%, 70%, .24);
}

.bg-were {
  @include bg-text('jesteśmy', -126px);
  @media (max-width: 1024px){
    &::after {
      bottom: -66px;
    }
  }
}

/* Statistics */

.statistics {
  background: linear-gradient(100.08deg, #5761B2 0%, #1FC5A8 100%);
  opacity: 0.5;
  color: white;
  .column {
    padding: 75px 20px;
  }
}
  

.stat-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  // column-width: 310px;
  margin-bottom: 50px;
  @media (max-width: 840px)
  {
    flex-direction: column;
    margin-bottom: 0;
  }
}
.stat-column {
  @media (max-width: 840px)
  {
    display:flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
}

.stat-item {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  height: 200px;
  width: 160px;
  text-align: center;
  font-weight: bold;
  max-width: 310px;
  margin: auto;
  &:nth-child(-n+4)::after {
    display: block;
    position: relative;
    content: '';
    height: 50px;
    width: 75px;
    border-bottom: 1px solid #FFF;
    @media (max-width:840px)
    {
      margin-bottom: 25px;
    }
  }
  svg {
    min-height: 40px;
    margin-bottom: 15px;
  }
  span {
    display: block;
    text-transform: uppercase;
  }
}
@media(min-width: 841px)
{
  .stat-list:not(.upper) {
    margin-bottom: 0;
    .stat-item{
      &:nth-child(-n+4)::after {
        display: none;
      }
    }
  }
}

@media(max-width:460px)
{
  .stat-item {
    &:nth-child(-n+4)::after {
      height: 35px;
    }
  }
}

.stat {
  font-family: var(--font-head);
  font-size: 40px;
  font-weight: bold;
}

.stat-text {
  min-height: 69px;
}

.infinity {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 54px;
}

/* Values */

.values {
  h2 {
    text-align: center;
    letter-spacing: 0px;
  }
}

.values-list-desktop {
  display: flex;
  flex-wrap: wrap;
  row-gap: 30px;
  column-gap: 40px;
  @media (max-width: 767px) {
    display: none;
  }
}

.values-list-mobile {
  display: flex;
  flex-direction: column;
  @media (min-width: 768px) {
    display: none;
  }
  .values-item {
    flex: unset;
    flex-direction: column;
    &:last-child {
      border-bottom: 1px solid #CCCCCC;
    }
  }
  .values-button {
    cursor: pointer;
    display: flex;
    height: 80px;
    align-items: center;
    border-top: 1px solid #CCCCCC;
  }
  .values-icon {
    display: flex;
    align-items: center;
    justify-items: center;
    margin-left: auto;
    margin-right: 15px;
    .minus-icon {
      display: none;
    }
  }

  .values-item-content {
    display: none;
    margin-bottom: 25px;
  }
}

.values-item {
  display: flex;
  flex: 1 1 500px;
}

.values-icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 40px;
  width: 40px;
  height: 40px;
  border: 1.5px solid #5761B2;
  border-radius: 5px;
  margin-right: 20px;
  @media(max-width:425px)
  {
    width: 30px;
    height: 30px;
    min-width: 30px;
  }
}

.values-item-content {
  width: 100%;
  h4 {
    margin: 0 0 20px 0;
    @media(max-width: 425px)
    {
    float: left;
    width: calc(100% - 16px);
    }
  }
  p {
  @media(max-width: 425px)
  {
    display:none;
  }
}
}

section.values > .bg {
  .bg-plus-1 {
    @include bg-item(15%, 80%, .25);
  }
  .bg-plus-3 {
    @include bg-item(40%, 22%, .25);
    transform: scale(.83)
  }
  .bg-arrow-bl {
    @include bg-item(82%, 45%, .15);
  }
  @media (max-width: 767px) {
    .bg-plus-1,
    .bg-plus-3,
    .bg-arrow-bl {
      display: none;
    }
  }
}

.bg-values {
  @include bg-text('wartości', -126px);
}

.accordion {
  display: none;
  height: 15px;
  width: 15px;
  font-size: 15px;
  font-weight: bold;
  float: left;
  margin: 0 0 26px 0;
  @media(max-width: 425px)
  {
    display:block;
  }
}

/* Location */

.location-section {
  background-color: #F9F9F9;
}

.location {
  display: flex;
  @media (max-width:1074px){
      flex-direction: column;
  }
}

.location-photo {
  position:relative;
  width: 500px;
  height: 492px;
  margin-right: 50px;
  @media (max-width:1074px){
    margin:auto;
    margin-bottom: 20px;
}
  @media (max-width:540px){
    width: 100vw;
    height: auto;
    left: -20px;
  }
}

.location-content {
  h2 {
    margin-bottom: 30px;
  }

  ul {
    margin: 0;
    padding: 0 0 0 20px;
    li {
      list-style-image: url(../../assets/images/about-list-style.svg);
      padding: 20px 0 0 20px;
    }
  }
}

/* Team */

.team-section {
    h2 {
        text-align: center;
        margin-bottom: 40px;
    }
}

.team {
  display: grid;
  grid-template-columns: repeat(4, 272.5px);
  grid-template-rows: repeat(4,272.5px);//272.5px
  gap: 50px;
  @media (max-width: 1320px)
  {
    grid-template-columns: repeat(3, 272.5px);
    grid-template-rows: repeat(5, 272.5px);
    margin: auto;
  }
  @media (max-width: 960px)
  {
    grid-template-columns: repeat(2, 272.5px);
    grid-template-rows: repeat(7, 272.5px);
  }
  @media (max-width: 640px)
  {
    grid-template-columns: repeat(2, calc((100vw - 60px) / 2));
    grid-template-rows: calc(((100vw - 60px) / 2) * 1.2) repeat(6, calc((100vw - 60px) / 2));
    gap: 45px 20px;
  }
}
.mobile {
  display: none;
  @media (max-width: 640px)
  {
    display:block;
  }
}
.k:not(.mobile),.p:not(.mobile)
{
  @media (max-width: 640px)
  {
    display:none;
  }
} 

.team-member {
  position: relative;
  background-color: #ccc;
  transition: all .3s ease;
  @media(min-width:641px)
  {
  &:hover {
    background-color: #1FC5A8;
    }
  }
}

.team-member:not(.k):not(.p) {
  img {
    width: 272.5px;
    height: 272.5px;
    @media (max-width: 640px)
    {
      width: calc((100vw - 60px) / 2);
      height: calc((100vw - 60px) / 2);
    }
  }
  @media (max-width: 960px)
  {
    width: 272.5px;
    height: 272.5px;
  }
  @media (max-width: 640px)
  {
    width: calc((100vw - 60px) / 2);
    height: calc((100vw - 60px) / 2);
  }
}

.k {
  grid-area: 1 / 1 / 3 / 2;
  img {
    width: 272.5px;
    height: 595px;
      @media (max-width: 640px)
      {
        width:calc((100vw - 60px) / 2);
        height: calc(((100vw - 60px) / 2) * 1.2);
      }
  }
  @media (max-width: 640px)
  {
    width:calc((100vw - 60px) / 2);
    height: calc(((100vw - 60px) / 2) * 1.2);
    grid-area: auto;
  }
}

.p {
  grid-area: 1 / 2 / 3 / 3;
  img {
    width: 272.5px;
    height: 595px;
      @media (max-width: 640px)
      {
        width: calc((100vw - 60px) / 2);
        height: calc(((100vw - 60px) / 2) * 1.2);
      }
  }
  @media (max-width: 640px)
  {
    width:calc((100vw - 60px) / 2);
    height: calc(((100vw - 60px) / 2) * 1.2);
    grid-area: auto;
  }
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 120px;
  margin: 0;
  background: rgba(87, 97, 178, .75);
  transition: .3s ease;
  span {
    display: block;
    text-align: center;
    color: #F5F5FA;
    line-height: 25px;
  }
}

.name {
  font-size: 18px;
  font-weight: bold;
}
.position {
  font-size: 14px;
}

@media(min-width: 641px)
{
.team-member:hover > div.intro {
  transition: .3s ease;
  opacity: 1;
}
}

.bg-values {
  @include bg-text('wartości', -126px);
  @media (max-width: 1024px){
    &::after {
      bottom: -66px;
    }
  }
}

.bg-team {
  @include bg-text('zespół', -126px);
  @media (max-width: 1024px){
    &::after {
      bottom: -66px;
    }
  }
}

.name-mobile {
  font-size:12px;
  font-weight: bold
}
.position-mobile{
  font-size:10px;
}
.desc-mobile {
  flex-direction: column;
  align-items: center;
  display:none;
  @media(max-width:640px)
  {
    display: flex;
  }
}