.swiper-slide {
  display: flex;
  .column {
    max-width: 1232px;
    padding: {
      left: 24px;
      right: 24px;
    }
    @media (max-width: 1280px) {
      width: unset;
      width: fit-content;
    }
    @media (max-width: 640px) {
      margin: 0;
    }
  }
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;

  &.swiper-pagination-bullet-active {
    background-color: var(--h-color);
  }
}

div.portfolio-wrapper div.project-slides div.swiper-container div.swiper-pagination span.swiper-pagination-bullet-active {
  background-color: #3B93AD;
}

#testimonial-swiper > .swiper-pagination-bullets {
    bottom: 0;
}

.swiper-button-prev {
  width: 35px;
  height: 35px;
  top: calc(50% - 11px);
  left: 2%;
  &::after {
    display: flex;
    width: 35px;
    height: 35px;
    content: '';
    background-image: url("../images/prev.svg");
    background-repeat: no-repeat;
    background-size: contain;
  }
  @media (max-width: 768px) {
    display: none;
  }
}

.swiper-button-next {
  width: 35px;
  height: 35px;
  top: calc(50% - 11px);
  right: 3%;
  &::after {
    display: flex;
    width: 35px;
    height: 35px;
    content: '';
    background-image: url("../images/next.svg");
    background-repeat: no-repeat;
    background-size: contain;
  }
  @media (max-width: 768px) {
    display: none;
  }
}

div.project-slides > div.swiper-container > div[class^="swiper-button"] {
  display: none;
}
@media (hover: hover) {
  #projects-swiper:hover div[class^="swiper-button"] {
    display: block;
  }
}

.desc-title {
  margin-bottom: 30px;
  @media (max-width: 1200px) {
    font-size: 2rem;
  }
  @media (max-width: 1128px) {
    display: none;
  }
}

.project-content-wrapper {
  min-width: 30%;
  flex: 1 1;
}
.portfolio-wrapper {
  display: flex;

  @media (max-width: 1023px) {
    flex-direction: column;
    align-items: center;
  }
  .project-slides {
    max-width: 720px;
    height: auto;
    margin-right: 50px;
    // flex
    flex: 1 1 auto;
    @media (max-width: 1200px) {
      max-width: 600px;
      margin-right: 25px;
    }
    @media (max-width: 1023px) {
      max-width: 720px;
      margin-right: 0;
      margin-bottom: 25px;
    }
    @media (max-width: 900px) {
      width: 102%;
    }
  }

  .swiper-wrapper {
    @media (max-width: 510px) {
      padding-bottom: 20px;
    }
  }

  .swiper-container {
    .swiper-img {
      width: 100%;
      height: 100%;
      margin: 0;
      overflow: visible;
    }
    .swiper-pagination {
      bottom: 25px;
      @media (max-width: 510px) {
        bottom: 0px;
      }
    }
  }

  @media (max-width: 1000px) {
    .swiper-container {
      .swiper-slide {
        display: block;
      }
      .swiper-img {
        width: 100%;
        margin: 0;
        height: auto;
        user-select: none;
      }
      .project-description:last-child {
        margin-bottom: 3em;
      }
    }
  }
}
