@mixin footer-icon($icon) {
  padding-left: 26px;
  &::before {
    display: inline-block;
    content: '';
    background-image: url($icon);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    height: 13px;
    width: 17px;
    position: absolute;
    transform: translate(-26px, 5px);
  }
}

footer {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: white;
  background-color: black;
  z-index: 0;
  min-height: 365px;
  background: url('../images/zabrze.jpg');
  background: {
    position: top center;
    size: cover;
  }
  a {
    transition: 200ms color;
    &:hover {
      color: white !important;
    }
  }
}

address {
  font-style: normal;
}

.footer-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1280px; // HD ready
  margin: 0 auto;
  padding: 0 24px;
}

.footer-contact-wrapper {
  position: relative;
  width: 100%;
  &::after {
    position: absolute;
    top: 15%;
    right: 0;
    content: "";
    display: block;
    width: 90px;
    height: 90px;
    border-top: 25px solid #989898;
    border-right: 25px solid #989898;
  }

  @media (max-width: 1024px) {
    &::after {
      top: 1%;
      right: -21px;
      transform: scale(.5);
    }
  }

  @media (max-width: 510px) {
    font-size: 12px;
    line-height: 1.5;
  }

  .schedule-meeting-button {
    a {
      padding: 7.5px 30px;
      border: 1px solid white;
      font-size: 12px;
    }
    @media (max-width: 510px) {
      height: 30px;
      line-height: 1.5;
      white-space: nowrap;
      a {
        font-size: 10px;
        line-height: 2;
        padding: 5px 25px;
      }
    }
  }
}

.footer-segment-wrapper {
  display: flex;
}

.footer-img {
  display: block;
  margin-right: 20px;
  width: 118px;
  height: auto;
  @media (max-width: 510px) {
    margin-right: 16px;
    width: 85px;
    height: 105px;
  }
  @media (max-width: 320px) {
    margin-right: 10px;
  }
}

.footer-contact {
  margin-top: 100px;
  display: flex;
  flex-wrap: wrap;
  @media (max-width: 1024px) {
    margin-top: 50px;
  }
}

.footer-contact-info {
  display: flex;
  flex-direction: column;
  color: #989898;
  font-style: normal;
  line-height: 1.8;
  margin: 0 0 35px 0;
  &:first-of-type {
    margin-right: 50px;
  }
  h4 {
    color: white;
    margin: 0 0 18px 0;
  }
  a {
    color: inherit;
  }
  @media (max-width: 510px) {
    margin: 0 0 35px 0;
    h4 {
      font-size: 14px;
    }
  }
}

.social-media .schedule-meeting-button {
    display: inline-block;
    border: 1px solid white;
    padding: 12px 0;
    a {
      color: #989898;
      border: none;
      margin-right: 0;
    }
  }

.leaflet-pane .leaflet-tile-pane {
  filter: invert(1) sepia(100%) hue-rotate(-230deg);
}

.leaflet-container .leaflet-control-attribution {
  color: rgba(255, 255, 255, 0.8);
  background-color: rgba(0, 0, 0, 0.8);

  a {
    color: rgba(255, 255, 255, 0.8);
  }
}

/* Address items */

.footer-contact-person {
  @include footer-icon('../images/icons/person-icon.svg');
}

.footer-contact-phone {
  @include footer-icon('../images/icons/phone-icon.svg');
}

.footer-contact-mail {
  @include footer-icon('../images/icons/mail-icon.svg');
}

.footer-address {
  @include footer-icon('../images/icons/location-pin-icon.svg');
}

.footer-copyright {
  @include footer-icon('../images/icons/copyright.svg');
}

.copyright-icon {
  @include footer-icon('../images/icons/copyright.svg');
  padding-left: 20px;
  &::before {
    height: 14px;
    width: 14px;
    transform: translate(-20px, 0);
  }
}

/* Clutch widget */
.widget {
  display: flex;
  width: 192px;
  height: 50px;
  margin: 0 30px 30px 50px;
  @media (max-width: 1130px) {
    width: 400px;
    margin: 0 30px 30px 0;
  }
}

/* Social media */

.social-media {
  margin: 0 0 30px 0;
  a {
    margin-right: 3em;
  }

  @media (max-width: 1100px) {
    width: 100%;
    text-align: center;
    flex-wrap: wrap;
    justify-content: flex-start;
    display: flex;
  }
  @media (min-width: 768px) {
    display: none;
  }
}

.social-media-inline {
  display: flex;
  align-items: center;
  margin-left: auto;
  a {
    margin-left: 36px;
  }
  @media(max-width: 768px) {
    display: none;
  }
}

@media (max-width: 510px) {
.footer-contact-person, .footer-contact-phone, .footer-contact-mail, .footer-address {
    padding-left: 18px;
    &::before {
      height: 12px;
      width: 12px;
      transform: translate(-18px, 6px);
    }
  }
}

.footer-copyright-styles {
  display: flex;
  margin-top: auto;
  background-color: black;
  height: 45px;
  color: var(--text-gray);
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 14px;
  span {
    padding-right: 50px;
    a {
      text-decoration-line: underline;
      color: var(--text-gray);
    }
  }
  @media (max-width: 580px) {
    height: 72px;
    span {
      margin: 5px 0;
    }
  }
}

.copyright-wrapper {
  display: flex;
  width: 100%;
  max-width: 1280px;  // HD ready
  margin: 0px auto;
  padding: 0 24px;
  align-content: center;
}

.copyright-notice {
  display: flex;
  align-content: center;
  flex-wrap: wrap;
  align-items: center;
  white-space: nowrap;
}

.cookies-text {
  a {
    color: #3B93AD;
  }
}

.policy-links > span:first-child {
  padding-right: 10px;
  &::after {
    position: relative;
    content: '|';
    padding-left: 10px;
  }
}